import Page from "../../Common/Page";
import { VerticalTimeline, VerticalTimelineElement }  from 'react-vertical-timeline-component';
import 'react-vertical-timeline-component/style.min.css';
import SchoolIcon from '@mui/icons-material/School';
import WorkIcon from '@mui/icons-material/Work';
import StarIcon from '@mui/icons-material/Star';
import { ReactComponent as GitIcon } from "../../Common/Icon/GitIcon.svg"
import { ReactComponent as JavascriptIcon } from "../../Common/Icon/JavascriptIcon.svg"
import { ReactComponent as ReactIcon } from "../../Common/Icon/ReactIcon.svg"
import { ReactComponent as PhpIcon } from "../../Common/Icon/phpIcon.svg"
import { ReactComponent as NodeIcon } from "../../Common/Icon/NodeIcon.svg"
import { ReactComponent as TypescriptIcon } from "../../Common/Icon/TypescriptIcon.svg"
import { ReactComponent as AndroidIcon } from "../../Common/Icon/androidIcon.svg"
import { ReactComponent as ArduinoIcon } from "../../Common/Icon/ArduinoIcon.svg"
import { ReactComponent as CppIcon } from "../../Common/Icon/cppIcon.svg"

import { styled, Tooltip, tooltipClasses, TooltipProps } from "@mui/material";

const INIT_PARAM = {
  className:"vertical-timeline-element--work",
  contentStyle:{ background: 'rgb(33, 150, 243)', color: '#fff' },
  contentArrowStyle:{ borderRight: '7px solid  rgb(33, 150, 243)' },
  dateClassName:"history_date",
  iconStyle:{background: 'rgb(33, 150, 243)', color: '#fff'}

}

const History : React.FC<any> = (props:any) => {

  return (
    <Page
    // color="#80cbc4"
    >
      <div className="history">
        <VerticalTimeline>

          <VerticalTimelineElement
            {...INIT_PARAM}
            icon={<WorkIcon />}
            date="2019 - 현재"
          >
            <h3 className="vertical-timeline-element-title">더존비즈온</h3>
            <h4 className="vertical-timeline-element-subtitle">Front-End 개발</h4>
            <p><Tooltips title="자체 개발 에디터를 활용하여 기업/개인간 전자 계약서 작성 서비스"><a href="#econtract">전자계약</a></Tooltips>,&nbsp;
            <Tooltips title="자체 개발 에디터를 활용하여 사내 근로 계약서 작성 서비스"><a href="#econtract">근로계약</a></Tooltips>,&nbsp;
            <Tooltips title="자체 개발 에디터를 활용하여 공인전자문서 수/발신할 수 있는 서비스"><a href="#edocs">전자문서</a></Tooltips>,&nbsp;
            <Tooltips title="SK Broadband사와 협업, CS업무관리할 수 있는 서비스"><a href="#skb">SKB 기업인터넷전화</a></Tooltips>,&nbsp;
              서비스 등 Front-End 신규 개발</p>
            <p style={{display:"flex"}}><ReactIcon/><TypescriptIcon/><JavascriptIcon/><NodeIcon/>
            <GitIcon/></p>
          </VerticalTimelineElement>

          <VerticalTimelineElement
            {...INIT_PARAM}
            icon={<WorkIcon />}
            date="2015 - 2018"
          >
            <h3 className="vertical-timeline-element-title">포스트미디어</h3>
            <h4 className="vertical-timeline-element-subtitle">Full-Stack 개발</h4>
            <p><Tooltips title="Facebook 좋아요 품앗이 사이트"><a href="#ilikesns">아이라이크 SNS</a></Tooltips>,&nbsp;
              <Tooltips title="가상화폐 모의투자 사이트"><a href="#simbit">SIMBIT</a></Tooltips> 사이트 Full-Stack 신규 개발 </p>
            <p style={{display:"flex"}}><PhpIcon/><JavascriptIcon/></p>
          </VerticalTimelineElement>

          <VerticalTimelineElement
            {...INIT_PARAM}
            icon={<SchoolIcon />}
            iconStyle={{ background: 'rgb(16, 204, 82)', color: '#fff' }}
            contentStyle={{ background: 'rgb(16, 204, 82)', color: '#fff' }}
            date="2010 - 2015"
          >
            <h3 className="vertical-timeline-element-title">동의과학대학교</h3>
            <h4 className="vertical-timeline-element-subtitle">컴퓨터정보계열</h4>
            <p><Tooltips title="레인지후드, Wifi, arduino, android를 이용한 모니터링, 원격 시스템 개발"><a href="#hood">제 6회 부산 앱 공모전 금상 수상</a></Tooltips></p>
            <p style={{display:"flex"}}><ArduinoIcon/><AndroidIcon/><CppIcon/></p>
          </VerticalTimelineElement>


        </VerticalTimeline>
      </div>
    </Page>
  );
}

const Tooltips = styled(({ className, ...props }: TooltipProps) => (
  <Tooltip {...props} arrow classes={{ popper: className }} />
))(({ theme }) => ({
  [`& .${tooltipClasses.arrow}`]: {
    color: theme.palette.common.white,
  },
  [`& .${tooltipClasses.tooltip}`]: {
    backgroundColor: theme.palette.common.white,
    color: theme.palette.common.black
  },
}));

type Props = {
  children?: React.ReactNode
};


export default History;
