const ProjectItem : React.FC<ProjectItemProps> = (props:ProjectItemProps) => {
  const {imageList,title,description,company,iconList,dateTime,id} = props
  const [imageDialog,setImageDialog] = useState<number|null>(null)

  return (
    <div className="project_item" id={id}>
      <h3>
        {title}
        <span>
        {company}
        </span>
      </h3>
      
      <p className="description">
        {description.map((v)=><>{v}<br/></>)}

      </p>
      <div className="icon_list">
        {iconList.map(R=><R/>)}
      </div>
      <span className="project_date">{dateTime}</span>
      <div className="image_box">
        {imageList.map((item,index)=>{
          return (
            <span
              key={`${id}_imageList_${index}`}
              onClick={()=>setImageDialog(index)}
              className="image_icon"
              style={{background:`url(${item})`,
              backgroundSize: "cover",
              backgroundPosition: "50% 50%"
            }}
              
            />
            )
        })}
      </div>
        <hr/>
        {imageDialog !== null &&
          <ImageViewer
            src={ imageList }
            currentIndex={ imageDialog }
            disableScroll={ false }
            closeOnClickOutside={ true }
            onClose={ ()=>setImageDialog(null) }
          />
        }
    </div>

  )
}


type ProjectItemProps ={
  children?: React.ReactNode
  imageList : string[]
  title : string
  description : string[]
  company : string
  iconList : React.FunctionComponent[]
  dateTime : string
  id : string
}


type Props = {
  children?: React.ReactNode
};


export default Project;
